
import AllRoutes from "./AllRoutes";
import { Link } from "react-router-dom";
import style from "../Styles/Navbar.module.css"
// import style from "./Nav.module.css"

import { Center, Square, Circle, HStack, Box } from '@chakra-ui/react'
import { Theme } from "../Components/Theme";
import { Button } from "../Components/Button";
import { MenuItems } from "../Components/MenuItems";
import styled from "styled-components";

function Navbar() {

    return <DIV id="nav-menu">

        <HStack className="nav cont">

            <Center className="theme">
                {/* <Box as='span' fontWeight='bold' fontSize='lg'>
            <Link to="/">Home</Link>
                </Box> */}
                <Theme />

            </Center>

            <Center className="home">
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/">
                        {/* <Button text={"Home"} /> */}
                        Home
                    </Link>
                </Box>

            </Center>

            <Center className="skills">
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/skills">Skills</Link>
                </Box>
            </Center>

            <Center className="project">
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/project">Project</Link>
                </Box>
            </Center>

            <Center className="about" >
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/about">About</Link>
                </Box>
            </Center>

            <Center className="contact" >
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/contact">Contact</Link>
                </Box>
            </Center>

            <Center className="resume">
                <Box as='span' fontWeight='bold' fontSize='lg'>
                    <Link to="/contact">Resume</Link>
                </Box>
            </Center>

            <Center className="menuItems">
                <Box as='span'>
                    <Link to="">
                        <MenuItems />
                    </Link>
                </Box>
            </Center>

        </HStack>






    </DIV>

}

export default Navbar;

const DIV = styled.div`

/* .nav{
    display: flex;
    justify-content: space-around;
     background-color: aqua; 
    height: 50px;
    
} */

.cont{
    /* border: 2px solid rgb(243, 255, 8); */
}

/* .allR{
    
    width: 90%;
    margin: auto;
} */

    .menuItems{
        display: none;
    }

    @media screen and (max-width: 1119px) {
      /* Your styles for large devices go here */
      .menuItems{
        display: block;
      }

      .about{
        display: none;
      }

      .contact{
        display: none;
      }


      
    }

    @media screen and (max-width: 885px) {
      /* Your styles for large devices go here */

      .home{
        display: none
      }

      .skills{
        display: none;
      }
      


      
    }

    @media screen and (min-width: 468px) {
    
     .nav{
    display: flex;
    justify-content: space-around;
     /* background-color: aqua;  */
    height: 50px;
     }
       

    }

    @media screen and (max-width: 467px) {
      /* Your styles for large devices go here */

     .theme{
        display: none;
     }

     .project{
        display: none;
     }

     .resume{
        display: none;
     }

     .menuItems{
        /* background-color: yellow; */
        display: flex;
        flex-direction: row-reverse;
        padding-right: 15px;
     }

     .nav{
        flex-direction: row-reverse;
        margin-top: 5px;
     }

     
       


      
    }



`